import React from 'react'

export default function Form() {
    return (
        <div className="input-form">
            <h2>Input Details</h2>
            <form action="POST" className="form">
                <div className="vehicle_id">
                    <h3>Vehicle ID</h3>
                    <input type="text" name="vehicle_id" id="vehicle_id"/>
                </div>

            
            </form>
        </div>
    )
}
