import React from 'react'

export default function Navbar() {
    return (
        <div className="navbar">
            <div className="logo">
                <img src="../img/ather.png" alt="Ather"/>
            </div>
            <div className="brand">
                <img src="../svg/steering-wheel.svg" alt="Steering Wheel"/>
                <span className="text dash">DASH</span>
                <span className="text">BOARD</span>
            </div>
            <div className="navLinks">
                <a href="/">Dash</a>
                <a href="/data">Data</a>
                <a href="/about">About</a>
            </div>
        </div>
    )
}
